@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.7.2/font/bootstrap-icons.css);
@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
@import url(https://kendo.cdn.telerik.com/themes/5.0.0/default/default-main.css);
@import url(https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css);
.chat-container{background-color:#e5e5e5;height:100vh;width:65%;display:flex;flex-direction:column}.chat-container .header-container{background-color:#28ADCD;font-size:1.5rem;color:white;display:flex;justify-content:center;padding:1rem 0}.chat-container .chatbox{height:100%;display:flex;flex-direction:column;overflow:auto;margin-top:2rem}.chat-container .chatbox .message{background-color:#63ABFFBD;padding:0.4rem;margin:0.5rem 2rem;margin-left:10rem;border-radius:0.6rem;border-top-right-radius:0;align-self:flex-end;box-shadow:0.1rem 0.1rem 0.1rem grey}.chat-container .chatbox .bot{background-color:white;margin:0.5rem 2rem;margin-right:10rem;align-self:flex-start;border-radius:0.6rem;border-top-left-radius:0}.chat-container form{display:flex;flex-direction:row}.chat-container form input{padding-left:2rem;border:none;width:100%}.chat-container form input:focus{outline:none}.chat-container form i{padding:1rem;background-color:white}.chat-container form i:hover{background-color:#28ADCD;color:white;transition-duration:0.5s}.login-container{float:left;width:35%;background-color:#800000;height:100vh;display:flex;justify-content:flex-start;align-items:center;flex-direction:column;justify-content:space-evenly}.login-container #roseLogo{width:80%}.login-container .webLink{font-size:1.5rem;color:white;border-radius:0.4rem;padding:0.5rem}.login-container .webLink a{margin-left:0.75rem}.login-container .webLink:hover{background-color:#fff;color:#800000;transition-duration:1s;cursor:pointer}body{margin:0;font-family:'Quicksand', sans-serif}body .App{display:flex;flex-direction:row}

